.Comunitycontainer {
  width: 100%;
  height: 1000px;
  /* background-color: rgb(128, 51, 51); */
  background-image: url("/public/images/Group 98.jpg");
  background-size: 100% 100%;
  z-index: -111;
  margin-top: -10px;
  /* margin-bottom: -180px; */
}

.FSComunity {
  width: 75%;
  margin: auto;
  color: white;
  padding: 140px 0;
}

.FSComunity h1,
.FSComunity p {
  text-align: center;
  margin-bottom: 20px;
}

.FSComunity h1{
  font-size: 40px;
  line-height: 49px;
  
}

.FSComunity p{
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 35px;
}

.Comunitycards {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  column-gap: 16px;
}

.Comunitycard {
  /* width: 250px; */
  border: 2px solid white;
  border-radius: 30px;
  overflow: hidden;
  /* background-color: rgba(17, 16, 16, 0.301); */
  
background: rgba(255, 255, 255, 0.3);
box-shadow: 0px 4px 24px -2px rgba(0, 0, 0, 0.2);
backdrop-filter: blur(10px);
}

.Comunitycard .img {
  width: 100%;
  height: 135px;
  overflow: hidden;
  background-size: cover;
  background-image: url(../../../public/images/news-card.png);
  /* margin-bottom: 20px; */
}

.ComunitycardBottom {
  padding: 16px 15px 22px 15px;
}

.ComunitycardBottom h1,
.ComunitycardBottom p,
.ComunitycardBottom span {
  text-align: left;
  margin: 0;
}

.ComunitycardBottom h1 {
  font-weight: 400;
font-size: 32px;
line-height: 40px;
letter-spacing: -0.02em;
}

.ComunitycardBottom p,
.ComunitycardBottom span {
  font-size: 14px;
  margin-top: 10px;
  color: rgb(190, 183, 183);
}

.fBton {
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
}

.fBton .fbtn1 {
  background: linear-gradient(134.5deg, #C25FFF 2.88%, #3D29D0 98.85%);
  box-shadow: -2px 4px 8px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  width: 180px;
  height: 40px;
  font-weight: 400;
  font-size: 14px;
  line-height: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding: 4px 4px 4px 32px;
}

.fBton .fbtn1 button {
  background: transparent;
  border: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 13px;
  color: white;
}

.fBton .fbtn1 .arrowicons {
  border: none;
  background: transparent;
  width: 34px;
  height: 34px;
}
